export const getSymbolsOracleInfoForLens = async (symbols) => {
  symbols = symbols.filter((s) => !onChainSymbols.includes(s))
  // const res = await getSignedValues(symbols.map((s) => normalizeOracleSymbol(s)))
  const res = await getOracleInfosFromRest(symbols.map((s) => normalizeOracleSymbol(s)))
  return symbols.reduce((acc, symbol, index) => {
    if (res[index]) {
      if (!acc.find((s) => s[0] === symbol)) {
        if (normalizeOracleSymbol(symbol) === symbol) {
          acc.push([symbol, res[index][2], '0'])
        } else {
          acc.push([symbol, '0', res[index][2]])
        }
      }
      return acc
    }
  }, [])
};
